.sidebar {
  display: inline-block;
  height: 92%;
  width: 29%;
  margin: 2%;
  border-radius: 9px;
  background-image: url("../assets/images/bg-sidebar-desktop.svg");
  background-size: cover;
}
.Step_No {
  height: 50%;
  padding: 4%;
  margin-top: 5%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  width: 80%;
  margin-left: auto;
  margin-right: auto;
}
.Step_row {
  display: flex;
  align-items: center;
  text-transform: uppercase;
}
.Step_column {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 5%;
}
.sidebar .circular {
  display: block;
  border-radius: 100%;
  width: 14%;
  border: 1px solid white;
  text-align: center;
  transform: scale(1.25);
  margin: 5%;
}

.circular:hover {
  background-color: lightblue;
  border: 1px solid none;
  color: black;
}
.active {
  background-color: lightblue;
  border: 1px solid none;
  color: black;
}
.circular p {
  display: inline-block;
  font-size: 10px;
  margin: 27% 0;
  text-align: center;
  font-weight: bold;
}
