.AddOn {
  width: 100%;
  height: 60%;
  margin: 7% auto;
}
.AddOn h1 {
  color: black;
  text-align: left;
  margin: 1.5% 0;
}
.AddOn p {
  color: grey;
  font-size: 14px;
}

div.services {
  display: flex;
  flex-direction: column;
  margin-top: 8%;
}
div.services__inner {
  display: flex;
  justify-content: space-between;
  height: 11vh;
  align-items: center;
  border: 1px solid rgb(184, 180, 180);
  border-radius: 6px;
  margin: 2% 0;
  width: 90%;
  padding: 0 7%;
}
div.services__inner > p {
  font-size: 12px;
  color: #1f1f86;
  font-weight: 600;
}

div.inner {
  display: flex;
  /* justify-content: space-between; */
  width: 65%;
}
div.inner > input[type="checkbox"] {
  margin-right: 9%;
}
div.services__inner h5 {
  color: rgb(25, 25, 73);
}
