.Summary {
  width: 100%;
  height: 60%;
  margin: 7% auto;
}
.Summary h1 {
  color: black;
  text-align: left;
  margin: 1.5% 0;
}
.Summary p {
  color: grey;
  font-size: 12px;
}

div.List {
  display: flex;
  flex-direction: column;
  margin-top: 6%;
}
.List .plan {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5%;
  background: #f6f1f1;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  height: 8vh;
  border-bottom: 1px solid #dcd4d4;
}
.Extra_Billing {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  padding: 2% 5%;
  background: #f6f1f1;
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
  height: 15vh;
}
.Extra_Billing .row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
.name {
  font-size: 12px;
  font-weight: 600;
}
p.amount {
  font-weight: 600;
  color: #1c1616;
}
.plan h5 {
  color: rgb(25, 25, 73);
  font-weight: 700;
  letter-spacing: 0.1px;
  font-family: Ubuntu;
  font-size: 14px;
}
.plan p {
  font-weight: 700;
  color: rgb(25, 25, 73);
  font-size: 14px;
}
a#GoToPlan {
  color: gray;
  font-size: 14px;
  font-weight: 600;
}

.total {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5%;
}
p.speacial {
  color: rgb(64, 64, 135);
  font-size: 18px;
  font-weight: 700;
}
