.Plan {
  width: 100%;
  height: 60%;
  margin: 7% auto;
}
.Plan h1 {
  color: black;
  text-align: left;
  margin: 1.5% 0;
}
.Plan p {
  color: grey;
  font-size: 14px;
}
p.free {
  color: rgb(6, 6, 59);
}
.Cards {
  height: 55%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 5%;
}
.yearly {
  height: 100%;
}
.monthly {
  height: 90%;
}
.card {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  border: 1px solid grey;
  /* height: 90%; */
  width: 31%;
  border-radius: 7px;
  box-sizing: border-box;
  padding-left: 3%;
}
.card:hover,
.active-card {
  cursor: pointer;
  background: aliceblue;
  border: 1px solid rgb(60, 60, 117);
}
.card > img {
  width: fit-content;
}
.card h4 {
  color: rgb(6, 6, 59);
}
.card p {
  font-size: 12px;
}

/* Toggle Button/Switch */
div.toggle {
  display: flex;
  justify-content: space-evenly;
  margin-top: 5%;
  height: 6vh;
  align-items: center;
  background: #eee;
  border-radius: 6px;
}
p.options {
  font-weight: bold;
}
p.color {
  color: rgb(25, 25, 73);
}
div.toggle input {
  display: none;
  cursor: pointer;
}
div.toggle label {
  position: relative;
  display: block;
  width: 50px;
  height: 25px;
  background: rgb(25, 25, 73);
  border-radius: 14px;
  cursor: pointer;
  /* transition: background 300ms linear; */
}

div.toggle label::before {
  position: absolute;
  top: 50%;
  left: 3px;
  transform: translateY(-50%);
  content: "";
  display: block;
  width: 20px;
  height: 20px;
  background: #eee;
  border-radius: 50%;
  transition: left 300ms linear;
}
/* + adjacent sibling selector */
div.toggle input:checked + label {
  background: rgb(25, 25, 73);
}
div.toggle input:checked + label::before {
  left: 27px;
}
