@import url("https://fonts.googleapis.com/css2?family=Architects+Daughter&family=Caveat&family=Courgette&family=Macondo&family=Ubuntu&display=swap");

* {
  margin: 0;
  padding: 0;
}
.App {
  display: flex;
  color: white;
  width: 65vw;
  height: 88vh;
  margin: 3% auto;
  border: 2px solid black;
}
.main_Column {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 43%;
  margin: 2% auto;
}
a#back {
  color: gray;
  text-decoration: none;
  font-size: 14px;
  font-weight: 600;
}
.hide {
  visibility: hidden;
}
a#back:hover {
  color: black;
}
div.btn-row {
  display: inline-flex;
  height: 15%;
  justify-content: space-between;
  align-items: center;
}
button.btn-next {
  width: 34%;
  height: 55%;
  font-weight: bold;
  background: rgb(25, 25, 73);
  outline: none;
  border: none;
  border-radius: 9px;
  color: white;
  font-size: 14px;
  cursor: pointer;
}
