.PersonalInfo {
  width: 100%;
  height: 60%;
  margin: 7% auto;
}
.PersonalInfo h1 {
  color: black;
  text-align: left;
  margin: 1.5% 0;
}
.PersonalInfo p {
  color: grey;
  font-size: 14px;
}
form#personal {
  margin-top: 6%;
}
form#personal > label {
  color: black;
  font-size: 11px;
  font-weight: 700;
  margin-bottom: 2%;
}
form#personal > input {
  display: block;
  width: 100%;
  height: 6vh;
  font-weight: bold;
  border-radius: 7px;
  box-shadow: none;
  padding: 1%;
  margin-top: 2%;
  margin-bottom: 1%;
  cursor: pointer;
  box-sizing: border-box;
  border: 1px solid #cccccc;
  /* outline: 1px solid rgb(190, 166, 190); */
  outline: none;
}
